<template>
  <div class="stopwatch-container">
    <div class="page-header">
      <h1>⏱️ 秒表</h1>
      <p class="page-description">精确计时工具，支持分段计时</p>
    </div>

    <div class="stopwatch-section">
      <div class="time-display">
        <div class="main-time">{{ formatTime(currentTime) }}</div>
        <div class="milliseconds">.{{ formatMilliseconds(currentTime) }}</div>
      </div>

      <div class="control-buttons">
        <el-button v-if="!isRunning && currentTime === 0" type="primary" size="large" @click="start">
          <i class="el-icon-video-play"></i> 开始
        </el-button>
        <el-button v-if="isRunning" type="warning" size="large" @click="pause">
          <i class="el-icon-video-pause"></i> 暂停
        </el-button>
        <el-button v-if="!isRunning && currentTime > 0" type="primary" size="large" @click="resume">
          <i class="el-icon-video-play"></i> 继续
        </el-button>
        <el-button v-if="isRunning" type="success" size="large" @click="lap">
          <i class="el-icon-circle-plus"></i> 分段
        </el-button>
        <el-button v-if="currentTime > 0" size="large" @click="reset">
          <i class="el-icon-refresh"></i> 重置
        </el-button>
      </div>

      <div class="lap-times" v-if="lapTimes.length > 0">
        <h3>分段时间</h3>
        <div class="lap-list">
          <div v-for="(lap, index) in lapTimes" :key="index" class="lap-item">
            <span class="lap-number">{{ lapTimes.length - index }}</span>
            <span class="lap-time">{{ formatTime(lap.time) }}.{{ formatMilliseconds(lap.time) }}</span>
            <span class="lap-diff" :class="{ fastest: lap.isFastest, slowest: lap.isSlowest }">
              {{ lap.diff > 0 ? '+' : '' }}{{ formatTime(Math.abs(lap.diff)) }}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StopwatchView',
  data() {
    return {
      currentTime: 0,
      isRunning: false,
      startTime: 0,
      pausedTime: 0,
      lapTimes: [],
      timer: null
    }
  },
  methods: {
    start() {
      this.isRunning = true
      this.startTime = Date.now() - this.pausedTime
      this.timer = setInterval(() => {
        this.currentTime = Date.now() - this.startTime
      }, 10)
    },
    
    pause() {
      this.isRunning = false
      this.pausedTime = this.currentTime
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    
    resume() {
      this.start()
    },
    
    reset() {
      this.isRunning = false
      this.currentTime = 0
      this.pausedTime = 0
      this.lapTimes = []
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    
    lap() {
      if (!this.isRunning) return
      
      const lapTime = this.currentTime
      const previousLap = this.lapTimes.length > 0 ? this.lapTimes[0].time : 0
      const diff = lapTime - previousLap
      
      this.lapTimes.unshift({
        time: lapTime,
        diff: diff,
        isFastest: false,
        isSlowest: false
      })
      
      this.updateLapStats()
    },
    
    updateLapStats() {
      if (this.lapTimes.length < 2) return
      
      const diffs = this.lapTimes.map(lap => lap.diff).filter(diff => diff > 0)
      const minDiff = Math.min(...diffs)
      const maxDiff = Math.max(...diffs)
      
      this.lapTimes.forEach(lap => {
        lap.isFastest = lap.diff === minDiff && lap.diff > 0
        lap.isSlowest = lap.diff === maxDiff && lap.diff > 0
      })
    },
    
    formatTime(ms) {
      const totalSeconds = Math.floor(ms / 1000)
      const minutes = Math.floor(totalSeconds / 60)
      const seconds = totalSeconds % 60
      return `${minutes.toString().padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`
    },
    
    formatMilliseconds(ms) {
      return Math.floor((ms % 1000) / 10).toString().padStart(2, '0')
    }
  },
  
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer)
    }
  }
}
</script>

